import CONST from '../_const';

export default function nav() {
  'use strict';

    let $hamburger = $('.js-hamburger');
    let $nav = $('.js-nav');
    let $body = $('.js-wrap');
    let $documentBody = $(document.body)[0];
    let $HTML_BODY = CONST.$htmlBody;
    let scrollPops;

    $hamburger.on('click', (e)=> {
        e.preventDefault();
        $hamburger.toggleClass(CONST.ACTIVE_CLASS);
        $nav.toggleClass(CONST.ACTIVE_CLASS);

        if($hamburger.hasClass(CONST.ACTIVE_CLASS)) {
                scrollLock();
            } else {
                scrollAble();
        }
    });

    let scrollLock = ()=> {
        scrollPops = $documentBody.scrollTop;
        $body.css({
            top: `-${scrollPops}px`
        });
        $body.addClass(CONST.LOCK_CLASS);
    };

    let scrollAble = ()=> {
        $body.removeClass(CONST.LOCK_CLASS);
        $body.css({
            top: ''
        });
        $documentBody.scrollTop = scrollPops;
    };
}


